<script>
import ClickOutside from 'vue-click-outside'
import { getTeacherInfo, getTeacherLessons, getTeacherVideos, checkAppiontment, checkVideo, payForVideo } from '../api/getdata'
import store from '../vuex/store'
import { Toast, MessageBox } from 'mint-ui'
export default {
  name: 'userInfor',
  data () {
    return {
      showLesson: 'thisWeek',
      topHeight: '',
      userInfo: {},
      active: 'tabContainer1',
      hasMask: false,
      isShow: false,
      menuLinks: [{
        link: '课程目录',
        isActive: true,
        tab: 'tabContainer1'
      }, /* {
        link: '套餐',
        isActive: false,
        tab: 'tabContainer2'
      }, */ {
        link: '教学视频',
        isActive: false,
        tab: 'tabContainer3'
      }],
      lessons: [],
      actions: [{
        link: '收藏'
      },
      {
        link: '分享'
      },
      {
        link: '举报'
      }],
      /* cardList: [],
      yearCard: {},
      timeCards: {},
      personalCard: {}, */
      videoList: [],
      freeVideos: [],
      paidVideos: [],
      normal: 'normal',
      personal: 'personal',
      showLessonDetails: false,
      hasmask: false,
      month: new Date().getMonth() + 1,
      days: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
      dateNum: [],
      thisWeek: [],
      nextWeek: [],
      times: ['06:00', '06:30', '07:00', '07:30', '08:00', '08:30', '09:00', '09:30', '10:00', '10:30', '11:00', '11:30', '12:00',
        '12:30', '13:00', '13:30', '14:00', '14:30', '15:00', '15:30', '16:00', '16:30', '17:00', '17:30', '18:00', '18:30',
        '19:00', '19:30', '20:00', '20:30', '21:00', '21:30', '22:00'],
      lessonThisWeek: [],
      lessonNextWeek: []
    }
  },
  directives: {
    ClickOutside
  },
  mounted () {
    function getWeek (i) {
      var now = new Date()
      var firstDay = new Date(now - (now.getDay() - 1) * 86400000)
      firstDay.setDate(firstDay.getDate() + i)
      var oMonth = Number(firstDay.getMonth() + 1)
      return oMonth + '-' + firstDay.getDate()
    }
    for (let k = 0; k < 7; k++) {
      this.thisWeek.push(getWeek(k))
    }
    // console.log(this.thisWeek)
    this.dateNum = this.thisWeek
    // console.log(this.dateNum)
    for (let k = 7; k < 14; k++) {
      this.nextWeek.push(getWeek(k))
    }
    this.fUserID = localStorage.getItem('fUserID')
    this.fInstructorId = localStorage.getItem('fInstructorId')
    this.getUserInfo()
    this.getLessons()
    // this.getCardList()
    this.getVideoList()
    window.scrollTo(100, 0)
  },
  watch: {
    active: function () {
      for (let i = 0; i < this.menuLinks.length; i++) {
        this.menuLinks[i].isActive = false
        if (this.active === this.menuLinks[i].tab) {
          this.menuLinks[i].isActive = true
        }
      }
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getUserInfo () {
      let data = getTeacherInfo({fInstructorId: this.fInstructorId})
      data.then(res => {
        // console.log(res.data)
        this.userInfo = res.data
        setTimeout(() => {
          this.topHeight = this.$refs.test.getBoundingClientRect().bottom * 10 / window.screen.width + 'rem'
        }, 0)
      })
    },
    getLessons () {
      let data = getTeacherLessons({fInstructorId: localStorage.getItem('fUserId')})
      // console.log(localStorage.getItem('fUserId'))
      data.then(res => {
        console.log(res)
        this.lessonThisWeek = res.data.beginTimeLesson
        this.lessonNextWeek = res.data.nextMondayLesson
      })
    },
    getLessonHeight (lesson) {
      var h = parseInt(lesson.lessonEndDateTime.split(':')[0]) - parseInt(lesson.lessonBeginDateTime.split(':')[0])
      var m = parseInt(lesson.lessonEndDateTime.split(':')[1]) - parseInt(lesson.lessonBeginDateTime.split(':')[1])
      var s
      if (m === 30) {
        s = 1
      } else if (m === -30) {
        s = -1
      } else {
        s = 0
      }
      var result = (2 * h + s) * 1.36 + 'rem'
      return result
    },
    getLessonTop (lesson) {
      var h = parseInt(lesson.lessonBeginDateTime.split(':')[0]) - 6
      var m = parseInt(lesson.lessonBeginDateTime.split(':')[1])
      var s
      if (m === 30) {
        s = 1
      } else {
        s = 0
      }
      var result = (2 * h + s + 1) * 1.36 + 'rem'
      return result
    },
    getLessonLeft (lesson) {
      var x = new Date(lesson.lessonBeginDate).getDay()
      if (x === 1) {
        return x * 1 + 0.53 + 'rem'
      } else if (x === 0) {
        x = 7
        return x * 1.0133 + 0.4 + 'rem'
      } else {
        return x * 1.0133 + 0.42 + 'rem'
      }
    },
    showLessonInfo (lessons, index) {
      document.addEventListener('touchmove', this.bodyscroll, false)
      document.body.parentNode.style.overflow = 'hidden'
      this.hasmask = true
      this.showLessonDetails = true
      this.lessonDetails = lessons[index]
    },
    hideDetails () {
      this.showLessonDetails = false
      this.hasmask = false
      document.removeEventListener('touchmove', this.bodyscroll, false)
      document.body.parentNode.style.overflow = 'auto'
    },
    /* 暂时不做
    getCardList () {
      axios.get(`http://47.96.76.137:8088/Practice/BundleByFinstor?fInstructorId=${this.fInstructorId}`)
        .then(res => {
          console.log(res.data)
          this.cardList = res.data.data
          if (this.cardList) {
            for (let i = 0; i < this.videoList.length; i++) {
              if (this.cardList[i].fBundleType === '年卡') {
                this.yearCard = this.cardList[i]
              } else if (this.cardList[i].fBundleType === '私教') {
                this.personalCard = this.cardList[i]
              } else {
                this.timeCards.push(this.cardList[i])
              }
            }
            return this.yearCard & this.personalCard & this.timeCards
          }
        })
    }, */
    getVideoList () {
      let data = getTeacherVideos({fInstructorId: localStorage.getItem('fInstructorId')})
      data.then(res => {
        console.log(res.data)
        this.videoList = res.data
        for (let i = 0; i < this.videoList.length; i++) {
          if (this.videoList[i].fIsOpen === '0') {
            this.freeVideos.push(this.videoList[i])
          } else {
            this.paidVideos.push(this.videoList[i])
          }
        }
        console.log(this.paidVideos)
        return this.freeVideos & this.paidVideos
      })
    },
    toggleTab (index) {
      window.scrollTo(100, 0)
      for (var i = 0; i < this.menuLinks.length; i++) {
        this.menuLinks[i].isActive = false
        this.menuLinks[index].isActive = true
        this.active = this.menuLinks[index].tab
      }
    },
    toggleMore () {
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
    },
    showDetails (index) {
      localStorage.setItem('fLessonId', this.lessons[index].fLessonId)
      this.$router.push('/home/teachers/lessonInfor')
    },
    playVideo (items, index) {
      if (items[index].fIsOpen === '0') {
        store.commit('getVideoInfo', items[index])
        this.$router.push('/home/mine/videoPlayer')
      } else {
        let data = checkVideo({videoId: items[index].fVideoId})
        // console.log(items[index].fVideoId)
        data.then(res => {
          console.log(res)
          if (res.status === '-1') {
            MessageBox.confirm('', {
              message: '确认付费观看?'
            }).then(action => {
              if (action === 'confirm') {
                let pay = payForVideo({videoId: items[index].fVideoId})
                pay.then(res => {
                  console.log(res)
                  if (res.status === '0') {
                    Toast({
                      message: '购买视频成功',
                      duration: 1500
                    })
                    store.commit('getVideoInfo', items[index])
                    this.$router.push('/home/mine/videoPlayer')
                  } else {
                    Toast({
                      message: '购买视频失败,请稍后再试',
                      duration: 1500
                    })
                  }
                })
              }
            }).catch(err => {
              if (err === 'cancel') {
                Toast({
                  message: '已取消',
                  duration: 1500
                })
              }
            })
          } else {
            store.commit('getVideoInfo', items[index])
            this.$router.push('/home/mine/videoPlayer')
          }
        })
      }
    },
    appiontment () {
      let data = checkAppiontment({lessonId: this.lessonDetails.lessonId})
      data.then(res => {
        console.log(res)
        if (res.status === '0') {
          store.commit('saveLessonId', this.lessonDetails.lessonId)
          this.$router.push('/home/teachers/lessonInfor')
        } else {
          this.hasmask = false
          this.showLessonDetails = false
          Toast({
            message: '该课程已预约',
            duration: 1500
          })
        }
      })
    },
    nextweek () {
      this.dateNum = this.nextWeek
      for (var i = 0; i < this.$refs.dates.length; i++) {
        if (this.$refs.dates[i].classList.contains('selected')) {
          this.$refs.dates[i].classList.remove('selected')
        }
      }
      this.showLesson = 'nextWeek'
    },
    previousWeek () {
      this.dateNum = this.thisWeek
      for (var i = 0; i < this.$refs.dates.length; i++) {
        if (this.$refs.dates[i].classList.contains('selected')) {
          this.$refs.dates[i].classList.remove('selected')
        }
      }
      this.showLesson = 'thisWeek'
    },
    showAll () {
      if (this.$refs.introduce.classList.contains('showDetail')) {
        // console.log(this.$refs.introduce)
        this.$refs.introduce.classList.remove('showDetail')
        this.topHeight = this.$refs.test.getBoundingClientRect().bottom * 10 / window.screen.width + 'rem'
      } else {
        // console.log(this.$refs.introduce)
        this.$refs.introduce.classList.add('showDetail')
        this.topHeight = this.$refs.test.getBoundingClientRect().bottom * 10 / window.screen.width + 'rem'
      }
    },
    toggleConcern () {
      console.log('concern')
    }
  }
}

</script>

