  .card {
    display: flex;
    margin-top: 0.8rem;
    border-bottom: 1px solid #e6e6e6;
    color: #333333;
    padding-bottom: 0.3rem;
    position: relative;
  }

  .cardLeft {
    width: 1.6rem;
    font-size: 0.4rem;
  }

  .cardLeft span {
    display: inline-block;
    width: 1rem;
  }
  .card {
    padding-bottom: 1.3rem;
  }

  .lessonLeft span {
    width: 1.7rem;
    display: inline-block;
    vertical-align: top;
  }

  .cardsInfor,
  .lessonInfor {
    list-style-type: none;
    text-align: start;
  }

  .cardsInfor li,
  .lessonLeft span,
  .lessonInfor li {
    font-size: 0.373333rem;
    padding: 0.1rem 0;
    line-height: 0.5rem;
  }

  .cardRight,
  .lessonRight {
    margin-left: 0.4rem;
  }

  .cardRight {
    margin-top: -0.1rem;
  }

  .moreInfor {
    position: absolute;
    top: 0;
    right: 0;
    background: #ffd200;
    font-size: 0.373333rem;
    color: #333333;
    border: none;
    outline: none;
    padding: 0.2rem 0.5rem;
    border-radius: 0.15rem;
  }

  .moreMenu {
    position: absolute;
    list-style-type: none;
    background: #f5f5f5;
    top: 1.2rem;
    right: 0.5rem;
    border-radius: 0.2rem;
    z-index: 99;
    padding: 0 0.65rem;
    box-shadow: -1px 1px 3px #f5f5f5;
  }

  .moreMenu li {
    width: 1.466667rem;
    color: #ff8309;
    padding: 0.5rem 0;
    font-size: 0.4266667rem;
  }

  .moreMenu li:nth-child(2) {
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
  }

  .textOrange {
    color: #ff8309;
    font-size: 0.373333rem;
    line-height: 1rem;
  }

  .buyNow {
    font-size: 0.373333rem;
    color: #333333;
    background: #ffd200;
    border-radius: 0.15rem;
    padding: 0.2rem 0.5rem;
    border: none;
    outline: none;
    bottom: 0.2rem;
    right: 0;
    position: absolute;
  }

  .cards .card:nth-child(3) {
    border: none;
  }

  .cards,
  .lessons,
  .allVideos {
    width: 100%;
  }

  .paidVideo {
    width: 100%;
    margin-top: 0.613333rem;
  }

  .freeVideo {
    width: 100%;
    margin-top: 0.373333rem;
  }

  .videoTitle {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .videoType {
    position: relative;
    font-size: 0.4266667rem;
    margin-left: 0.16rem;
    color: #333333;
  }

  .videoType::before {
    display: block;
    position: absolute;
    left: -0.16rem;
    content: "";
    width: 0.05rem;
    height: 0.45rem;
    background: #ffd200;
  }

  .showMore {
    display: flex;
    color: #666666;
    font-size: 0.4rem;
    outline: none;
    border: none;
    background: transparent;
    align-items: center;
  }

  .videos {
    width: 100%;
  }

  .video {
    width: calc(50% - 0.08rem);
    float: left;
    position: relative;
    overflow: hidden;
  }

  .video:nth-child(even) {
    margin-left: 0.16rem;
  }

  .videoCover {
    width: 100%;
    margin: 0.32rem 0;
  }

  .videoCover img {
    width: 100%;
    height: 100%;
  }

  .videoDetails {
    text-align: start;
  }

  .videoName {
    font-size: 0.426667rem;
    color: #333333;
  }

  .videoDiscription {
    display: inline-block;
    font-size: 0.3466667rem;
    color: #666666;
    margin-top: 0.15rem;
    line-height: 0.45rem;
  }

  .clearFix {
    clear: both;
  }

  .fixedArea {
    background: #fff;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
  }

  .mask {
    z-index: 999;
    position: absolute;
    top: -10rem;
    width: 100%;
    height: 2000%;
    background: #ffffff;
    opacity: 0;
  }

  .mask1 {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background: #000000;
    opacity: .24;
    z-index: 9999;
  }

  .usertype {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: -0.1rem;
    bottom: -0.1rem;
    color: #fffcfc;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    border: 2px solid #fffcfc;
    background: #ffd200;
    font-size: 0.373333rem;
  }

  .dateSection {
    width: 100%;
    padding: 0.5rem 0 0.2rem 0;
    background: #fff;
    z-index: 99;
    position: fixed;
    left: 0;
  }

  .date,
  .week {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-left: 1.85rem;
  }

  .date {
    display: flex;
    list-style: none;
    color: #333333;
    margin-top: 0.35rem;
    position: relative;
  }

  .week {
    display: flex;
    list-style: none;
    color: #666666;
  }

  .week li {
    margin: 0 0.15rem;
    width: 0.69333rem;
    text-align: center;
    font-size: 0.34667rem;
  }

  .date li {
    margin: 0 0.15rem;
    width: 0.69333rem;
    text-align: center;
    font-size: 0.4rem;
  }

  .month {
    position: absolute;
    left: 0.5rem;
    top: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #ffd200;
  }

  .previous,
  .next {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 0.21132rem;
    height: 0.4rem;
  }

  .next {
    right: 1.8rem;
    top: 0.08rem;
  }

  .previous {
    left: -0.5rem;
    top: 0.08rem;
    transform: rotate(180deg);
  }

  .lessonForm {
    margin-top: 1.2rem;
    position: relative;
    margin-bottom: 0.5rem;
  }

  .timeArea {
    width: 100%;
    margin: 0 auto;
  }

  .times {
    display: flex;
    height: 1.35rem;
    width: 100%;
    border-bottom: 0.01rem solid #f5f5f5;
    list-style: none;
    position: relative;
  }

  .times::after {
    content: '';
    display: block;
    position: absolute;
    width: 1rem;
    height: 100%;
    background: #ffffff;
    border-bottom: 0.01rem solid #ffffff;
    top: 0;
    right: -0.2rem;
  }

  .time {
    display: inline-block;
    width: 1.5rem;
    text-align: start;
    font-size: 0.37333rem;
    color: #666666;
    position: absolute;
    background: #fff;
    top: 1.08333rem;
  }

  .lesson {
    position: absolute;
    width: 0.90667rem;
    color: #fff;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .normal {
    background: #6ecd40;
  }

  .personal {
    background: #ffd200;
  }

  .lessonDetails {
    width: calc(100% - 0.8rem);
    border: 1px solid #f5f5f5;
    position: fixed;
    top: 5rem;
    left: 0.4rem;
    z-index: 10000;
    background: #fff;
    border-radius: 0.15rem;
  }

  .content {
    width: calc(100% - 0.4rem);
    margin: 0 auto;
  }
  .infor {
    display: flex;
    margin: 0.38rem auto;
    padding: 0.3rem 0;
    color: #333333;
    margin-bottom: 0;
  }

  .lessonLeft span {
    width: 1.65rem;
    display: inline-block;
    vertical-align: top;
    font-size: 0.373333rem;
  }

  .lessonDetail {
    list-style-type: none;
    text-align: start;
  }

  .lessonDetail li {
    font-size: 0.373333rem;
    padding: 0.1rem 0;
  }

  .lessonDetail li:first-child {
    padding-top: 0;
  }

  .lessonRight {
    position: relative;
    margin-left: 0.48rem;
  }

  .accountNum {
    border-top: 1px solid #e6e6e6;
    text-align: start;
    padding-top: 0.3rem;
  }

  .number {
    font-size: 0.4rem;
    color: #333333;
  }

  .textOrange {
    font-size: 0.4rem;
    color: #ff8309;
  }

  .deleteLesson {
    width: 1.5rem;
    height: 0.6rem;
    background: #ffd200;
    border: none;
    outline: none;
    border-radius: 0.15rem;
    color: #fff;
    font-size: 0.4rem;
  }

  .delete {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-bottom: 0.3rem;
  }

  .mask1 {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background: #000000;
    opacity: .24;
    z-index: 9999;
  }

  .grids {
    display: flex;
    width: calc(100% - 2.3rem);
    justify-content: space-between;
    margin-left: 1.5rem;
    overflow: hidden;
  }

  .grid {
    display: block;
    width: 0.90667rem;
    border-left: 1px solid #e6e6e6;
    border-right: 1px solid #e6e6e6;
  }

  .times:first-child .grid {
    border: none;
  }

  .full {
    background: #f5f5f5;
    color: #666666;
  }

  .symbol {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 0.53333rem;
    background: #ffd200;
    font-size: 0.4rem;
    position: absolute;
    left: 3rem;
    top: 0.2rem;
    transform: rotate(45deg);
  }

  .concern {
    position: absolute;
    right: 1rem;
    top: 0.2rem;
    width: 1.7333rem;
    height: 0.7733rem;
    border: 1px solid #333333;
    border-radius: 0.38667rem;
    background: transparent;
    color: #333333;
    outline: none;
    font-size: 0.4rem;
    font-weight: 500;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .concern img {
    width: 0.34rem;
    height: 0.34rem;
    vertical-align: middle;
  }

  .concern span,
  .concerned span {
    vertical-align: middle;
  }
</style>



